.about {
    position: absolute;
    height: 100vh;
    width: 100vw;
  
    &:after {
      content: "";
      height: 340px;
      width: 340px;
      border: 1px solid $color-2;
      border-radius: 50%;
      position: absolute;
      bottom: -200px;
      left: -80px;
      z-index: -1;
  
      @media screen and (max-width: 810px){
        height: 200px;
        width: 200px;
        bottom: -110px;
      }
      @media screen and (max-width: 430px){
        // display: none;
        left : -500px;
      }
    }
  
    .navigation {
      top: 5px;
      left: 60%;
      font-size: 1.1rem;
      line-height: 20px;
      transform: translateY(-100%);
      animation: navProject 1s ease forwards;
  
      @media screen and (max-width: 650px){
        left: 70%;
      }
    
      @keyframes navProject {
        to {
          transform: translateY(0);
        }
      }
  
      &:before {
        left: -120px;
        top: -230px;
      }
  
      .nav-projects {
        transform: translateX(67px);
        padding: 0 20px;
      }
    }
    .logo {
      top: 15px;
      left: 50%;
      transform: translateX(-50%);
      transform: scale(0);
      animation: logoProject 2s ease forwards;
    
      @keyframes logoProject {
        to {
          transform: scale(1);
        }
      }
    }
    .project-main {
      margin: 6rem 1rem 0;
      display: grid;
      grid-template-columns: 50% 50%;
      position: relative;
  
  
      @media screen and (max-width: 810px){
        grid-template-columns: 50% 50%;
      }
     
      @media screen and (max-width: 766px){
        grid-template-columns: 1fr;
        grid-template-rows:  1fr;
      }
      @media screen and (max-height: 600px){
        margin: 3rem 1rem 0;
      }
    
      .project-container1 {
        margin: 1rem 2rem 0;
        display: grid;

        grid-template-columns: 50% 50%;
        position: relative;
        text-align: right;
        top: 20px;
        @media screen and (max-width: 766px){
          grid-template-columns: 1fr;
          grid-template-rows:  1fr;
        }
      .project-content {
        
        margin-right: 10px;
        
        @media screen and (max-width: 766px){
          display: flex;
          justify-content: space-between;
          align-items: center;
          padding: 0 .5rem;
        }
      
        h1 {
          font-size: 3rem;
          margin-bottom: 40px;
          font-family: $font-2;
          color: $color-2;
          transform: scaleX(0);
          animation: transformTitle 1.5s ease forwards;
          transform-origin: 150%;
          position: relative;
          @media screen and (max-width: 1224px){
            font-size: 2rem;
            right: 40px;

           }
          @media screen and (max-width: 812px){
            font-size: 1.8rem;
            top: -50px;
          }
          @media screen and (max-width: 450px){
            font-size: 1.2rem;
            top: -20px;
          }
  
          @keyframes transformTitle {
            to {
              transform: scaleX(1);
            }
          }
        }
        p {
          margin-bottom: 10px;
          font-size: 1.7rem;
          font-weight: 700;
          position: relative;

  
          @media screen and (max-width: 766px){
            margin: 0;
            top: -59px;
            // left: 1px;

          }
          @media screen and (max-width: 450px){
            font-size: .9rem;
            top: -35px;
            left: -19px;

          }
          @media screen and (max-width: 1224px){
            right: 40px;
 
         }
          
          &:after {
            content: "";
            height: 440px;
            width: 440px;
            background: $color-3;
            border-radius: 50%;
            position: absolute;
            top: -228px;
            left: 50px;
            z-index: -10;
  
            @media screen and (max-width: 810px){
              height: 220px;
              width: 220px;
              left: -30px;
            }
          }
        }
        li {
          font-size: 2.0rem;
          color: $color-4;


  
          @media screen and (max-width: 766px){
            font-size: 1.2rem;
            position: relative;
            top: -35px;

          }
          @media screen and (max-width: 400px){
            font-size: 0.8rem;
            position: relative;
            top: -18px;
          }
        }
      }
      .img-content {
        position: relative;
        text-align: left;
        margin-left: 10px;

       
        
        @media screen and (max-width: 766px){
          text-align: center;
          margin: 0 auto;
        }
  
        .img-container {
          border-radius: 5px;
          box-shadow: 0px 2px 4px rgba(51, 51, 51, 0.26);
          width: 320px;
          height:320px;
          border-radius: 50%;
          overflow: hidden;
          position: relative;
          top: -39px;
        
          
          @media screen and (max-width: 1224px){
            height: 250px;
            width: 250px;
            left: -40px;
         }
         
          @media screen and (max-width: 766px){
            
            display: none;
          }
          @media screen and (max-width: 400px){
            height: 150px;
            width: 150px
          }
  
          &:hover span,
          span:hover {
            visibility: visible;
            transform: translateY(0);
          }
  
          img {
            overflow: hidden;
            object-fit: cover;
            object-position: 0%;
            width: 100%;
            height: 100%;
            border-radius: 50%;
            position: absolute;
            transition: 0.4s ease-out;
            position: relative;
  
            &:hover,
            span:hover img {
              transform: scale(1.2);
              filter: opacity(8%) contrast(150%);
            }
              
         
          }
          span {
            position: absolute;
            bottom: 20px;
            left: 30px;
            width: 100%;
            padding: 1rem;
            margin: 0 auto;
            line-height: 34px;
            font-size: 1.5rem;
            visibility: hidden;
            transition: 0.5s ease;
            transform: translateY(200px);
  
            h3 {
              padding-bottom: 10px;
              color: rgb(2, 121, 148);
            }
          }
        }
      }
    }
    .button-container {
      width: 150px;
  
      a {
        .button {
          display: flex;
          align-items: center;
          justify-content: center;
          color: $color-1;
          background: $color-2;
  
          @media screen and (max-width: 410px){
            width: 100px;
            font-size: .85rem;
          }
  
          &:hover {
            color: $color-2;
            background: $color-3;
          }
        }
      }
    }
  
