.dynamic-text {
  position: relative;
  display: flex;

  .simply {
    position: relative;
    top: 60px ;
    left: 150px;
    color:rgb(228, 126, 147);
    @media screen and (max-width: 650px){
      font-size: 2rem;
    }
    @media screen and (max-width: 1200px){
      font-size: 4rem;
    }
    @media screen and (max-width: 900px){
      font-size: 3rem;
    }
    @media screen and (max-width: 690px){
      font-size: 2rem;
      left: 50px;

    }
    @media screen and (max-width: 460px){
      font-size: 1.5rem;
    }
    @media screen and (max-width: 350px){
      font-size: 1.2rem;
    }
    
  }

  #text-target {
    position: relative;
    display: flex;
    width: 300px;
    text-align: left;
    top: 60px ;
    left: 150px;
    animation: color 30s alternate infinite;
    perspective: 200px;
    transform: translateX(26px);
    @media screen and (max-width: 650px){
      font-size: 2rem;
    }
    @media screen and (max-width: 1200px){
      font-size: 4rem;
    }
    @media screen and (max-width: 900px){
      font-size: 3rem;
    }
    @media screen and (max-width: 690px){
      font-size: 2rem;
      left: 50px;

    }
    @media screen and (max-width: 460px){
      font-size: 1.5rem;
    }
    @media screen and (max-width: 350px){
      font-size: 1.2rem;
    }
   
  }
  .word {
    position: absolute;
  }
  @keyframes color {
    0% {
      color: $color-2;
    }
    50% {
      color: $color-4;
    }
    100% {
      color: $color-2;
    }
  }
  .letter {
    transform: translateZ(200px);
  }
  @keyframes anim {
    25% {
      transform: translateZ(0px);
      opacity: 1;
    }
    32% {
      opacity: 0;
      transform: translateZ(-280px) translateX(200px);
    }
  }
}
